export interface Simulation {
  portfolioSnapshots: PortfolioSnapshot[];
  rebalanceLogs: RebalanceLog[];
  variables: Variables;
  annualizedTQQQRate?: number;
  annualizedQQQRate?: number;
  annualizedSig9lRate?: number;
}

export interface Variables {
  initialMoney: number;
  startDate: string;
  endDate: string;
  rebalanceDays: number;
  targetRate: number;
  CashDayRate: number;
  TargetRatio: number;
  SpikeRate: number;
  DropRate: number;
  BigDropRate: number;
}

export interface PortfolioSnapshot {
  date: string;
  investments: Investments;
  cumulativeRateSinceRebalance: number;
  nextTarget: number;
  peak: number;
  pullback: number;
  lastRebalanceDate: string;
  nextRebalanceDate: string;
}

export interface RebalanceLog {
  date: string;
  total: number;
  nextTarget: number;
  cumulativeRateSinceLastRebalance: number;
  rebalanceType: RebalanceType;
}

export enum RebalanceType {
  Rebalance = "Rebalance",
  Reset = "Reset",
  Skip = "Skip",
  StillDropping = "Still Dropping",
  BigPullback = "Big Pullback",
}

export interface Investments {
  Total: number;
  MockTotalQQQ: number;
  MockTotalTQQQ: number;
  TQQQ: number;
  Cash: number;
  Ratio: number;
}

export interface MarketData {
  QQQ: Record<string, number>;
  TQQQ: Record<string, number>;
}

export interface ChartPoint {
  time: string;
  value: number;
}

export type ChartData = ChartPoint[];

export type MultiSeriesChartData = Record<string, ChartData>;
